@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 500;
}
  
  .home,
  .classes,
  .docs,
  .sign-up {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .classes {
    background-image: url(/ju-production24/static/media/classroom.906e51a8.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .docs {
    background-image: url(/ju-production24/static/media/pic.3a8163c9.jpg);
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .sign-up {
    background-image: url(/ju-production24/static/media/pic.3a8163c9.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }

  
.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 22.5px;
    display: flex;
    align-items: center;
  }

  .logo_image {
    height: 50px;
    width: 65px;
    border-radius: 18px;
    padding: 4px;
    margin-right: 5px;
  }

  .logo_text {
  position: relative;
  font-family: sans-serif;
  font-size: 30px;
  letter-spacing: 2px;
  overflow: hidden;
  background: linear-gradient(60deg, rgb(255, 255, 255), rgb(252, 252, 252), rgb(252, 252, 252));
  background-repeat: no-repeat;
  background-size: 70%;
  animation: animate 2.5s linear infinite;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: rgba(255, 238, 0, 0.85);
}


@keyframes animate {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}


  @media only screen and (max-width: 1200px ) {
    .navbar-logo {
      font-size: 17px;
    }
  }

  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: rgb(251, 255, 7);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid rgb(255, 217, 0);
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: rgb(244, 248, 12);
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }

    .logo_text {
      font-size: 25px;
      margin-bottom: -15px;
    }

    .logo_image {
      margin-bottom: -15px;
    }
  }


  
  .hero-container {
    background: url(/ju-production24/static/media/unibg2.19fee3a4.jpg) center center/cover no-repeat;
    height: 95vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }

  /* neon effect */
  .neon {
    font-weight: 400;
    color: #fdd;
    text-shadow: 0 0 5px rgb(238, 13, 13),
      0 0 5px rgb(236, 19, 19),
      0 0 15px rgb(228, 24, 24);
  }

  /* animation */
  .animation {
    text-align: center;
    color: white;
    font-size: 10rem;
    font-weight: 600;
    line-height: 85px;
    letter-spacing: 10px;
    
    text-shadow: 10px 10px 25px rgb(180, 146, 44),
    -10px 10px 25px rgb(185, 155, 54),
    -10px -10px 25px rgb(197, 162, 46),
    10px -10px 25px rgb(0, 0, 0);


    animation: swing 3s linear infinite alternate;
  }
  
  @keyframes swing {
    from {
      transform: rotate(2.5deg);
    }
    to {
      transform: rotate(-2.5deg);
    }
  }

  /* Jad Uni animation */
  .jadUni {
    position: relative;
    font-family: sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 4px;
    overflow: hidden;
    background: linear-gradient(90deg, rgb(255, 255, 255), #fff, rgb(255, 255, 255));
    background-repeat: no-repeat;
    background-size: 70%;
    animation: animate 4s ease-in-out infinite;
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: rgba(1, 15, 92, 0.8);
    text-shadow: 2px 2px 1px rgba(199, 192, 192, 0.7);
  }
  
  @keyframes animate {
    0% {
      background-position: -500%;
    }
    100% {
      background-position: 500%;
    }
  }
  
  .hero-container > h3 {
    color: #fff;
    font-size: 50px;
    margin-top: -100px;
  }
  
  .hero-container > p {
    margin-top: 9px;
    color: #fff;
    font-size: 52px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }

  .hero-container > h2 {
    margin-top: 10px;
    color: #fff;
    font-size: 38px;
  }
  
@media screen and (max-width: 960px){
    .hero-container > h3 {
        font-size: 50px;
        text-align: center;
        margin-top: -150px;
      }
      .hero-container > p {
        font-size: 40px;
        margin-top: 10px;
      }
}

@media screen and (max-width: 768){
    .hero-container > h3 {
        font-size: 50px;
        margin-top: -100px;
      }

      .hero-container > p{
          font-size: 30px;
      }
}

  
@media screen and (max-width: 650px){
    .hero-container > h3 {
        font-size: 35px;
        margin-top: -150px;
    }

    .hero-container > p{
        font-size: 25px;
    }

    .hero-container > h2 {
      font-size: 30px;
    }
}

@media screen and (max-width: 420px){
    .hero-container > h3 {
        font-size: 35px;
        margin-top: -175px;
    }

    .hero-container > p{
        font-size: 22.5px;
    }

    .hero-container > h2 {
      font-size: 25px;
    }
}

@media screen and (max-width: 350px){
  .hero-container > h3 {
      font-size: 25px;
      margin-top: -175px;
  }

  .hero-container > p{
      font-size: 15px;
  }

  .hero-container > h2 {
    font-size: 20px;
  }
}

/* Button */

.bookBtn {
  float: left;
	min-width: 125px;
	max-width: 250px;
	display: block;
  margin: 1em;
  font-weight: 800;
	padding: 0.5em 1em;
  border: none;
  border-radius: 25px;
	background: none;
	color: inherit;
	position: relative;
	z-index: 1;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	-moz-osx-font-smoothing: grayscale;
}

.bookBtn:focus {
  outline: none;
}


.button--nina {
	padding: 0 2em;
	background: #fffb02;
	color: rgb(0, 0, 0);
	overflow: hidden;
	transition: background-color 0.3s;
}
.button--nina.button--inverted {
	background: rgb(0, 0, 0);
	color: #fffb02;
}
.button--nina > span {
	display: inline-block;
	padding: 1em 0;
	opacity: 0;
	color: #fffb02;
	transform: translate3d(0, -10px, 0);
	transition: transform 0.3s, opacity 0.3s;
	transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.button--nina::before {
	content: attr(data-text);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 1em 0;
	transition: transform 0.3s, opacity 0.3s;
	transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.button--nina:hover {
  background: rgb(0, 0, 0);
	color: #fffb02;
}
.button--nina:hover::before {
	opacity: 0;
	transform: translate3d(0, 100%, 0);
}
.button--nina:hover > span {
	opacity: 1;
	transform: translate3d(0, 0, 0);
}
.button--nina:hover > span:nth-child(1) {
	transition-delay: 0.045s;
}
.button--nina:hover > span:nth-child(2) {
	transition-delay: 0.09s;
}
.button--nina:hover > span:nth-child(3) {
	transition-delay: 0.135s;
}
.button--nina:hover > span:nth-child(4) {
	transition-delay: 0.18s;
}
.button--nina:hover > span:nth-child(5) {
	transition-delay: 0.225s;
}
.button--nina:hover > span:nth-child(6) {
	transition-delay: 0.27s;
}
.button--nina:hover > span:nth-child(7) {
	transition-delay: 0.315s;
}
.button--nina:hover > span:nth-child(8) {
	transition-delay: 0.36s;
}
.button--nina:hover > span:nth-child(9) {
	transition-delay: 0.405s;
}
.button--nina:hover > span:nth-child(10) {
 transition-delay: 0.45s;
}
.cards {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards_container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards_wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards_items {
    margin-bottom: 24px;
    flex-wrap: wrap;
  }
  
  .cards_item {
    display: flex;
    flex: 1 1;
    margin: 0 1rem;
    margin-bottom: 25px;
    border-radius: 10px;
    min-width: 300px; 
  }
  
  .cards_item_link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards_item_pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards_item_img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards_item_img:hover {
    transform: scale(1.1);
  }
  
  .cards_item_info {
    padding: 20px 30px 30px;
  }
  
  .cards_item_text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content_blog_container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards_items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards_item {
      margin-bottom: 2rem;
    }
  }
